import { useState } from "react";
import "../../../assets/css/head/eyes.css"
import { i18nUtil } from "../../../assets/js/common/i18nUtil";
import { AudioOutline, UpCircleOutline } from "antd-mobile-icons";
function TextInput(props) {
    const [sendText, setSendText] = useState('');
    return (
        <div className="text-input" style={{ display: props.show ? undefined : 'none' }}>
            <div className="text-send-input flex-center">
                <div className="text-left-btn flex-center" onClick={() => {
                }}>
                    <AudioOutline />
                </div>
                <input type="text" value={sendText} onInput={(e) => {
                    setSendText(e.target.value);
                }} placeholder={i18nUtil.getText('askMe') + '...'} />
                <div className={sendText === '' ? "text-right-btn flex-center" : "text-right-btn ok flex-center"} onClick={() => {
                    props.handleSendText(sendText);
                    setSendText('');
                }}>
                    <UpCircleOutline />
                </div>
            </div>
        </div>
    );
}
export default TextInput;