export const commonConstant = {
    WINDOWS: 'Windows',
    MAC: 'Mac',
    IOS: 'ios',
    ANDROID: 'Android',
}
export const commonUtil = {
    formatTimeNumber(timeNumber) {
        return parseInt(timeNumber) >= 10 ? timeNumber : "0" + timeNumber;
    },
    getDeviceType() {
        var userAgent = navigator.userAgent;
        if (/iPhone|iPad|iPod/.test(userAgent)) {
            return commonConstant.IOS;
        } else if (/Android/.test(userAgent)) {
            return commonConstant.ANDROID;
        } else if (/Macintosh/.test(userAgent) && /Intel Mac OS X/.test(userAgent)) {
            return commonConstant.MAC;
        } else if (/Windows/.test(userAgent)) {
            return commonConstant.WINDOWS;
        } else {
            return commonConstant.MAC;
        }
    },
    scrollBodyToBottom() {
        const element = document.documentElement || document.body;
        if (element) {
            element.scrollTop = element.scrollHeight + 80 + 90;
        }
    },
    scrollBodyToTop() {
        const element = document.documentElement || document.body;
        if (element) {
            element.scrollTop = 0;
        }
    },
    scrollToTop(id) {
        const element = document.getElementById(id);
        if (element) {
            element.scrollTop = 0;
        }
    },
    scrollToBottom(id) {
        const element = document.getElementById(id);
        if (element) {
            element.scrollTop = element.scrollHeight;
        }
    },
    sortArray(array, sortKey, asc) {
        if (asc == null) {
            asc = true;
        }
        if (!array || array.length === 0) {
            return array;
        }
        for (let i = 0; i < array.length - 1; i++) {
            for (let j = 0; j < array.length - i - 1; j++) {
                let temp;
                if (asc) {
                    if (array[j + 1][sortKey] < array[j][sortKey]) {
                        temp = array[j];
                        array[j] = array[j + 1];
                        array[j + 1] = temp;
                    }
                } else {
                    if (array[j + 1][sortKey] > array[j][sortKey]) {
                        temp = array[j];
                        array[j] = array[j + 1];
                        array[j + 1] = temp;
                    }
                }
            }
        }
        return array;
    },
}