import "../../../../assets/css/body/hand.css"
import { brainStoreType } from "../../../../assets/js/head/brainUtil";
import ChatRecordPage from "./ChatRecordPage";
function FeatureListPage(props) {
    return (
        <div className="feature-list" style={{ display: props.show ? undefined : 'none' }}>
            <div className="feature-item-row">
            </div>
            <div className="feature-item-row" onClick={() => {
                const aliGptToken = prompt('请输入 token');
                if (aliGptToken && aliGptToken !== '') {
                    localStorage.setItem(brainStoreType.ALI_GPT_TOKEN, aliGptToken);
                    window.location.reload();
                }
            }}>
                <div className="feature-mini-item flex-center" style={{ wordBreak: 'break-all', padding: '10px', width: 'calc(100% - 20px)' }}>
                    通义千问Token:<br />
                    {localStorage.getItem(brainStoreType.ALI_GPT_TOKEN)}
                </div>
            </div>
            <ChatRecordPage chatRecords={props.chatRecords} loadChatRecords={() => props.loadChatRecords()} showChatRecord={(record) => props.showChatRecord(record)} />
        </div>
    );
}
export default FeatureListPage;