import { DeleteOutline } from "antd-mobile-icons";
import { brainStoreType } from "../../../../assets/js/head/brainUtil";

function ChatRecord(props) {
    return (
        <div className="feature-item-row">
            <div className="feature-mid-item">
                <div className="chat-record-head">
                    <div className="chat-record-head-title">对话记录</div>
                    <div className="chat-record-head-operate flex-center" onClick={() => {
                        if (window.confirm('确定清空聊天记录吗？')) {
                            localStorage.removeItem(brainStoreType.CHAT_LIST);
                            props.loadChatRecords();
                        }
                    }}><DeleteOutline /></div>
                </div>
                <div style={{ display: props.chatRecords.length !== 0 ? undefined : 'none' }} className="chat-record-list">
                    {
                        props.chatRecords.map((record, index) => {
                            return (
                                <div key={index} className="chat-record-list-item flex-center" onClick={() => {
                                    props.showChatRecord(record);
                                }}>
                                    {record.content}
                                </div>
                            );
                        })
                    }
                </div>
                <div style={{ display: props.chatRecords.length === 0 ? undefined : 'none' }} className="chat-record-list">
                    <div className="sys-empty">没有任何对话</div>
                </div>
            </div>
        </div>
    );
}
export default ChatRecord;