import { commonConstant, commonUtil } from "../common/commonUtil";
import logo from "../../img/logo.png";
import { brainStoreType, brainStoreUtil } from "./brainUtil";

export const mouthUtil = {
    helloText() {
        brainStoreUtil.add(brainStoreType.CHAT_LIST, { from: 'assistant', content: '您好，有什么可以帮您？' });
    },
    allowBackTalk() {
        let deviceType = commonUtil.getDeviceType();
        if (deviceType === commonConstant.MAC || deviceType === commonConstant.WINDOWS) {
            if ('Notification' in window) {
                Notification.requestPermission().then((permission) => {
                    if (permission === "granted") {
                        console.log("用户已授予通知权限");
                    } else if (permission === "denied") {
                        console.log("用户拒绝了通知权限");
                    } else {
                        console.log("用户未做出选择");
                    }
                });
            } else {
                console.log("不支持通知");
            }
        } else if (deviceType === commonConstant.IOS) {
            // let pushUrl = localStorage.getItem('pushUrl');
            // if (pushUrl && pushUrl !== '' && pushUrl !== 'null') {
            //     return;
            // }
            // localStorage.setItem('pushUrl', prompt('请输入推送地址'));
        }
    },
    backTalk(title, content) {
        let deviceType = commonUtil.getDeviceType();
        if (deviceType === commonConstant.MAC || deviceType === commonConstant.WINDOWS) {
            if ('Notification' in window && Notification.permission === "granted") {
                let notification = new Notification(title, {
                    // body: content,
                    icon: logo,
                    // tag: "通知的唯一标识（可选）",
                    // vibrate: [200, 100, 200],  // 设备震动模式（可选）
                    // sound: "声音文件路径（可选）",
                    // dir: "ltr",  // 文字方向（可选）
                    // lang: window.navigator.language,  // 语言（可选）
                    // data: {  // 自定义数据（可选）
                    //     key: "value"
                    // }
                });
                // 可以为通知添加事件监听器
                notification.onclick = function () {
                    console.log("用户点击了通知");
                    // 可以在这里添加打开相关页面等操作
                };
                notification.onshow = function () {
                    console.log("通知已显示");
                };
                notification.onerror = function () {
                    console.log("通知出错");
                };
                notification.onclose = function () {
                    console.log("通知已关闭");
                };
            }
        } else if (deviceType === commonConstant.IOS) {
            // let pushUrl = localStorage.getItem('pushUrl') + '/' + title + "/" + content;
            // if (!title || title === '') {
            //     pushUrl = localStorage.getItem('pushUrl') + '/'+ title;
            // }
            // pushUrl += "?sound=shake";
            // fetch(pushUrl);
        }
    }
}