const lang = navigator.language ? navigator.language.split("-")[0] : 'en';
const cnTextMap = {
    'feature': '功能',
    'chat': '对话',
    'send': '发送',
    'askMe': '问我',
    'helloText': '你好，有什么可以帮您',
    'ok': '知道了',
    'thinking': '正在思考',
    'deleteAll': '清空',
};
const enTextMap = {
    'feature': 'Feature',
    'chat': 'Chat',
    'send': 'Send',
    'askMe': 'Ask Me',
    'helloText': 'Hello, What can i do for you',
    'ok': 'OK',
    'thinking': 'Thinking',
    'deleteAll': 'Delete All',
};
export const i18nUtil = {
    getText(code) {
        let res = '';
        switch (lang) {
            case 'zh': res = cnTextMap[code];
                break;
            case 'en': res = enTextMap[code];
                break;
            default: res = cnTextMap[code];
        }
        return res;
    }
}