import '../assets/css/assistant.css'
import AssistantFace from './head/face/AssistantFace';
import TextInput from './head/eyes/TextInput';
import FeatureListPage from './body/hand/feature/FeatureListPage';
import ChatAnswerPage from './body/hand/chat/ChatAnswerPage';
import { brainStoreType, brainStoreUtil, brainThinkUtil } from '../assets/js/head/brainUtil';
import { useEffect, useState } from 'react';
import { mouthUtil } from '../assets/js/head/mouthUtil';
import { Modal } from 'antd-mobile';
import { commonUtil } from '../assets/js/common/commonUtil';
import { i18nUtil } from '../assets/js/common/i18nUtil';
function AssistantPage() {
    const [answerContent, setAnswerContent] = useState("");
    const [askContent, setAskContent] = useState("");
    const [showChatAnswer, setShowChatAnswer] = useState(false);
    const [thnkStatus, setThnkStatus] = useState(false);
    const [chatRecords, setChatRecords] = useState([]);
    function loadNotification() {
        const notificationDataTypes = [brainStoreType.TODO_LIST];
        // 初始化通知定时器
        for (const dataType of notificationDataTypes) {
            const dataList = brainStoreUtil.getDataList(dataType);
            for (const data of dataList) {
                // 跳过已完成
                if (data.ok) {
                    continue;
                }
                if (data.okDates && data.okDates.indexOf(new Date().toLocaleDateString()) !== -1) {
                    continue;
                }
                // 重复的计算时间点
                if (data.repeatType) {
                    const nowDate = new Date();
                    const timeStrSplit = data.timeStr.split(":");
                    switch (data.repeatType) {
                        case 'everyday':
                            data.timestamp = new Date(nowDate.getFullYear(), nowDate.getMonth(), nowDate.getDate(), timeStrSplit[0], timeStrSplit[1], 0).getTime();
                            break;
                        case 'workday':
                            if (nowDate.getDay() !== 0 && nowDate.getDay() !== 6) {
                                data.timestamp = new Date(nowDate.getFullYear(), nowDate.getMonth(), nowDate.getDate(), timeStrSplit[0], timeStrSplit[1], 0).getTime();
                            }
                            break;
                        case 'sat':
                            if (nowDate.getDay() === 6) {
                                data.timestamp = new Date(nowDate.getFullYear(), nowDate.getMonth(), nowDate.getDate(), timeStrSplit[0], timeStrSplit[1], 0).getTime();
                            }
                            break;
                        case 'sun':
                            if (nowDate.getDay() === 0) {
                                data.timestamp = new Date(nowDate.getFullYear(), nowDate.getMonth(), nowDate.getDate(), timeStrSplit[0], timeStrSplit[1], 0).getTime();
                            }
                            break;
                        default: ;
                    }
                }
                if (!data.timestamp) {
                    continue;
                }
                const endMils = data.timestamp - new Date().getTime();
                if (endMils > 0) {
                    // 定时提醒
                    setTimeout(function () {
                        if (!handleAlertData(dataType, data)) {
                            return;
                        }
                    }, endMils);
                } else if (endMils <= 0) {
                    if (!handleAlertData(dataType, data)) {
                        continue;
                    }
                }
            };
        }
    }
    function handleAlertData(dataType, data) {
        let content = "现在是 " + new Date(data.timestamp).toLocaleTimeString() + " 你该" + data.title + "了";
        // 已删除，不提醒
        let newestData = brainStoreUtil.getById(dataType, data.dataId);
        if (!newestData) {
            return false;
        }
        if (newestData.ok) {
            return false;
        }
        if (newestData.okDates && newestData.okDates.indexOf(new Date().toLocaleDateString()) !== -1) {
            return false;
        }
        // 通知
        mouthUtil.backTalk(content);
        // alert(content);
        Modal.alert({
            content: content,
        });
        // 更新数据已完成
        if (data.repeatType) {
            let okDates = newestData.okDates;
            if (!okDates) {
                okDates = [];
            }
            okDates.push(new Date().toLocaleDateString());
            newestData.okDates = okDates;
        } else {
            newestData.ok = true;
        }
        brainStoreUtil.updateById(dataType, newestData);
    }
    async function handleSendText(sendText) {
        if (!sendText || sendText === '') {
            return;
        }
        // 添加用户消息
        setAskContent(sendText);
        setAnswerContent(i18nUtil.getText("thinking") + "...");
        const userMsgId = brainStoreUtil.add(brainStoreType.CHAT_LIST, { from: 'user', content: sendText });
        // 本地处理用户消息
        let localThinkRes = brainThinkUtil.localHandleText(sendText);
        setShowChatAnswer(true);
        if (localThinkRes && localThinkRes !== '') {
            brainStoreUtil.add(brainStoreType.CHAT_LIST, { from: 'assistant', content: localThinkRes, userMsgId: userMsgId });
            setAnswerContent(localThinkRes);
            loadNotification();
            loadChatRecords();
        } else {
            // 云处理用户消息
            setThnkStatus(true);
            let netThinkRes = await brainThinkUtil.netHandleText(sendText);
            let loadingChatData = { from: 'assistant', userMsgId: userMsgId };
            if (netThinkRes && netThinkRes !== '') {
                loadingChatData.content = netThinkRes;
                loadingChatData.md = true;
            } else {
                loadingChatData.content = "哎呀，我不太明白";
            }
            brainStoreUtil.add(brainStoreType.CHAT_LIST, loadingChatData);
            setAskContent(sendText);
            setAnswerContent(loadingChatData.content);
            setThnkStatus(false);
            setShowChatAnswer(true);
            loadChatRecords();
            setTimeout(function () {
                commonUtil.scrollToTop('chatAnswer');
            }, 10);
        }
    }
    function loadChatRecords() {
        let chatList = brainStoreUtil.getDataList(brainStoreType.CHAT_LIST);
        let chatRecords = chatList.filter(chatItem => chatItem.from === 'user');
        chatRecords = commonUtil.sortArray(chatRecords, "createTime", false);
        setChatRecords(chatRecords);
    }
    function showChatRecord(record) {
        let chatList = brainStoreUtil.getDataList(brainStoreType.CHAT_LIST);
        let chatAnswer = chatList.filter(chatItem => chatItem.userMsgId === record.dataId)[0];
        setAskContent(record.content);
        setAnswerContent(chatAnswer.content);
        setShowChatAnswer(true);
        setTimeout(function () {
            commonUtil.scrollToTop('chatAnswer');
        }, 10);
    }
    useEffect(function () {
        loadNotification();
        loadChatRecords();
        // eslint-disable-next-line
    }, []);
    return (
        <div className='assistant'>
            <div className='assistant-head'>
                <AssistantFace thnkStatus={thnkStatus} />
                <ChatAnswerPage show={showChatAnswer} askContent={askContent} answerContent={answerContent} close={(value) => { setShowChatAnswer(false) }} />
            </div>
            <div className='assistant-main'>
                <FeatureListPage show={true} chatRecords={chatRecords} loadChatRecords={() => loadChatRecords()} showChatRecord={(record) => {
                    showChatRecord(record);
                }} />
            </div>
            <div className='assistant-bottom'>
                <TextInput show={true}
                    handleSendText={(sendText) => {
                        handleSendText(sendText);
                    }} />
            </div>
        </div>
    );
}
export default AssistantPage;